:root {
  --primary: #ff9901;
  --textDarkGrey: #5f6368;
  --secondaryGrey: #3c3c4399;
  --border-grey: #dadada;
  --lightGold: #fac476;
  --whiteColor: #ffffff;
  --black: black;
  --headerGrey: #747474;
  --greyText: #8a8a8e;
}

* {
  font-family: Cabin !important;
}

th {
  font-weight: 500 !important;
}

.text-greyText {
  color: var(--greyText);
}

.text-red {
  color: red;
}

.bg-primary {
  background: var(--primary);
}

.text-primary {
  color: var(--primary);
}

.text-secondaryGrey {
  color: var(--secondaryGrey);
}

.custom-h-60 {
  height: 60px !important;
}

.custom-w-60 {
  width: 60px !important;
}

.custom-w-140 {
  width: 120px !important;
}

.rbc-event,
.rbc-day-slot .rbc-background-event {
  background: #ff5f0040 !important;
}

.font-9 {
  font-size: 9px;
}

.h-38 {
  height: 38px !important;
}

.max-w-167 {
  max-width: 167px !important;
}

