.calendar-column:nth-child(odd) {
  border-left: 1px solid var(--border-grey);
}
.calendar-column:nth-child(even) {
  border-left: 1px dashed var(--border-grey);
}
.calendar-column:nth-child(even) span {
  opacity: 0;
}
.current-time {
  border-left: 2px solid var(--headerGrey);
}

.time-xs {
  font-size: 9px;
}
